<template>
  <!-- 车辆信息 -->
  <div class="container">
    <div class="carInfo">
      <div class="item">
        <div>企业名称</div>
        <el-input v-model="input" placeholder="请输入内容"></el-input>
      </div>
      <div class="item">
        <div>企业联系人</div>
        <el-input v-model="input" placeholder="请输入内容"></el-input>
      </div>
      <div class="item">
        <div>联系人电话</div>
        <el-input v-model="input" placeholder="请输入内容"></el-input>
      </div>
      <div class="item itemAll">
        <div>提交申请资料</div>
        <el-upload
        action="https://jsonplaceholder.typicode.com/posts/"
        list-type="picture-card"
        :on-preview="handlePictureCardPreview"
        :on-remove="handleRemove">
        <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
        <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
      </div>
    </div>
    <div class="elStepsCon">
      <el-button type="primary">提交审批</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "carInfo",
  data() {
    return {
      input: '',
      active: 0,
    };
  },
  methods: {
    next() {
      if (this.active++ > 2) this.active = 0;
    },
  },
};
</script>
<style scoped>
.container {
  background-color: #fff;
  height: 100%;
  padding: 20px 0;
}
.elStepsCon {
  width: 400px;
  margin: 20px auto;
  text-align: center;
}
.carInfo {
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
}
.carInfo .item {
  width: 50%;
  padding: 10px 0;
  font-size: 15px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.itemAll {
  width: 60% !important;
  align-items: flex-start !important;
}
.itemAll  img{
  width: 150px;
  height: 150px;
}
.itemAll  .el-upload-list{
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.carInfo .item div {
   width: 20.33333333%;
   font-size: 15px;
}
.carInfo .item .el-input {
  width: 200px;
  color: #999;
}
</style>


